<template>
  <div class="todo-list">
        <TodoListItem 
        v-for="(item,index) in todos" 
        :key="index" 
        :todo-item="item"
        @change-state="item.completed = $event.target.checked"
        
        ></TodoListItem>
    </div>
</template>

<script>
import TodoListItem from './TodoListItem'
export default {
    name:'todolist',
    props:['todos'],
    components: {
        TodoListItem
    }
}
</script>

<style scoped>

.todo-list{
  display: grid;
  row-gap: 14px;
}


</style>